:root {
    /*Menu*/
    --before-text: #33333380;
    --hover-text:#333;
    --btn-border-color: #333;
    /*Footer*/
  }

/*Menu*/
  .navigation_menu__1aiRy {
      background-color: transparent;
      padding: 0;
      margin: 5vh 4vw;
      position: fixed;
      top: 0;
      left: 0;
      height: 100vh;
  }
  
  .navigation_menu__1aiRy > div > a {
    position: relative;
    display: block;
    margin: 5px 10px;
    padding: 8px 10px;
    width: 100px;
    font-size: 1.5rem;
    text-align: center;
    color: #33333380;
    color: var(--before-text);
    border: 2px solid transparent;
    outline: none;
    transition: all .2s;
  }
  
  .navigation_menu__1aiRy > div > a::before,
  .navigation_menu__1aiRy > div > a::after {
    position: absolute;
    content: '';
    width: 0;
    height: 0;
    border: 3px solid transparent;
  }
  
  .navigation_menu__1aiRy > div > a::before {
    top: -2px;
    left: -2px;
  }
  
  .navigation_menu__1aiRy > div > a::after {
    bottom: -2px;
    right: -2px;
  }
  
  .navigation_menu__1aiRy > div > a:hover {
    color: #333;
    color: var(--hover-text);
  }
  
  .navigation_menu__1aiRy > div > a:hover::before,
  .navigation_menu__1aiRy > div > a:hover::after {
    width: 100%;
    height: 100%;
  }
  
  .navigation_menu__1aiRy > div > a:hover::before {
    border-bottom-color: #333;
    border-bottom-color: var(--btn-border-color);
    border-left-color: transparent;
    transition: width .3s;
  }

  .navigation_hidenMenu__H3NGc {
    pointer-events: none;
  }

  .navigation_menuBtn__3NpXG {
    margin: 20px 0;
  }

  /*Footer*/
  .navigation_footer__sQa8g {
    background-color: transparent;
    color: #fff;
    height: 7vh;
    text-align: center;
    padding: 0;
    margin: 0 auto;
    font-size: 1.5rem;
  }

  .navigation_footer__sQa8g > span {
    display: inline-block;
    vertical-align: center;
    margin: 15px auto;
  }
