@font-face {
  font-family: 'icomoon';
  src:  url('fonts/icomoon.eot?au8nj8');
  src:  url('fonts/icomoon.eot?au8nj8#iefix') format('embedded-opentype'),
    url('fonts/icomoon.ttf?au8nj8') format('truetype'),
    url('fonts/icomoon.woff?au8nj8') format('woff'),
    url('fonts/icomoon.svg?au8nj8#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icon-instagram:before {
  content: "\ea92";
}
.icon-instagram:hover {
  color: transparent;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
  background-clip: text;
  transition: 400ms;
}
.icon-twitter:before {
  content: "\ea96";
}
.icon-twitter:hover {
  color: #1DA1F2;
  transition: 400ms;
}
.icon-github:before {
  content: "\eab0";
}
.icon-github:hover {
  color: #000;
  transition: 400ms;
}
